.active-tab {
    color: navy;
    background-color: whitesmoke;
    border-radius: 10px;
}

.list-group-item:hover {
    color: red;
    background-color: grey;
    border-radius: 10px;
    cursor: pointer;
    transition: .4s;
}

#aboutme {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
    color: black;
    background-color: whitesmoke;
    opacity: 0.8;
    margin: 1em 0;
    border: 2px solid black;
    border-radius: 10px;
}

#aboutme-text {
    margin: 0 1em 1em 1em;
    font-size: 1.25em;
    font-weight: 600;
}

#profile-picture {
    width: 300px;
    height: 300px;
    border-radius: 50%;
    margin: 20px;
    border: 4px solid grey;
}

#profile-picture:hover {
    opacity: 50%;
    transition: .4s;
}

#root {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    width: 100vw;
}

body {
    margin: 0;
    font-family: Trebuchet MS,Lucida Sans Unicode,Lucida Grande,Lucida Sans,Arial,sans-serif;
}

h1, h2, h3, p, ul {
    margin: 0;
}

h2 {
    font-size: 2em;
    margin-top: 10px;
}

header, footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 75px;
    background: linear-gradient(#323232, #d3d3d3);
    margin: 0;
}

header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
}

footer {
    justify-content: center;
    background: linear-gradient( #d3d3d3, #323232);
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
}

footer a {
    margin: 0 16px;
}

footer a:hover {
    opacity: 50%;
    transition: .4s;
}

ul {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
}

li {
    font-weight: 600;
}

h1 {
    font-size: 2.5rem;
    padding: 0 16px;
}

.portfolio-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 0;
    padding: 75px 0;    
    background: linear-gradient(black, gray, black);

}

.project-container {
    display: flex;
    justify-content: center;
    width: 75%;
    flex-wrap: wrap;
    color: whitesmoke;
}

.project-container img {
    height: 50%;
    width: 100%;
}

.cards {
    background-color: darkslategrey;
    box-shadow: 10px 10px 5px black;
    height: fit-content;
    width: 46%;
    border: white solid 1px;
    margin: 1em;
    border-radius: 5px;
    padding: .5em;
}

.project-button,
.form-button {
    font-size: 1.1em;
    padding: 6px 10px;
    border-radius: 10px;
    border: 1px solid black;
    font-weight: 500;
    background: linear-gradient(rgb(20, 20, 20), gray, rgb(20, 20, 20));
    color: whitesmoke;
    box-shadow: 5px 5px 5px rgb(15, 15, 15);
}

.project-button:first-of-type {
    margin-right: 10px;
}

.project-button:hover,
.form-button:hover {
    opacity: 50%;
    transition: .4s;
    cursor: pointer;
}

.cards h4 {
    font-size: 2em;
    margin: 5px 0;
}

.cards h5 {
    font-size: 1.25em;
    margin: 5px 0;
}

.cards p:last-of-type {
    margin-bottom: 5px;
}

.contact-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
    margin: 16px 0;
    padding: 0 1em;    
    background-color: whitesmoke;
    border-radius: 10px;
    border: 2px solid black;
    box-shadow: 10px 10px 5px gray;
}

.contact-form h2 {
    padding-bottom: 10px;
}


.formline {
    display: flex;
    width: 100%;
    margin-bottom: 10px;
}

.formline label {
    display: flex;
    width: 20%;
    margin-right: 10px;
    justify-content: flex-end;
}

.formline input,
.formline textarea {
    display: flex;
    justify-content: flex-start;
    width: 70%;
    padding: 5px;
    border: 2px solid black;
    border-radius: 5px;
}

.formline input:focus,
.formline textarea:focus {
    outline: none;
    border: 2px solid navy;
    box-shadow: 0 0 10px navy;
}

.error-text {
    color: red;
    font-size: 1.2em;
    margin: 0;
}

.form-button {
    margin-bottom: 10px; 
}

.skills-list {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    padding: 0;
}

.file-img {
    width: 50px;
    height: 50px;
}

#profile-picture:hover,
.file-img:hover {
    opacity: 50%;
    transition: .4s;
}

.skills-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: whitesmoke;
}

.resume-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10px;
    padding-top: 16px;
}

h3 {
    font-size: 1.5em;
    margin-bottom: 10px;
}

#root {
    width: 100%;
}